'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { getStudentResults, getEvaluations, getParentReviews } from '@/lib/mockData';
import Link from 'next/link';

export default function DashboardPage() {
  const { user, isLoading } = useAuth();
  const router = useRouter();
  const [stats, setStats] = useState({
    totalResults: 0,
    totalEvaluations: 0,
    totalReviews: 0
  });

  useEffect(() => {
    if (!isLoading && !user) {
      router.push('/login');
      return;
    }

    if (user) {
      // Load statistics
      const results = getStudentResults();
      const evaluations = getEvaluations();
      const reviews = getParentReviews();

      setStats({
        totalResults: results.length,
        totalEvaluations: evaluations.length,
        totalReviews: reviews.length
      });
    }
  }, [user, isLoading, router]);

  if (isLoading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-lg">Loading...</div>
      </div>
    );
  }

  if (!user) {
    return null; // Will redirect to login
  }

  const getRoleSpecificContent = () => {
    switch (user.role) {
      case 'admin':
        return {
          title: 'Administrator Dashboard',
          description: 'Manage all aspects of the school system',
          cards: [
            {
              title: 'Student Results',
              description: 'View and manage all student academic results',
              link: '/results',
              count: stats.totalResults
            },
            {
              title: 'Evaluations',
              description: 'Review student and teacher evaluations',
              link: '/evaluations',
              count: stats.totalEvaluations
            },
            {
              title: 'Parent Reviews',
              description: 'Monitor parent feedback and reviews',
              link: '/reviews',
              count: stats.totalReviews
            }
          ]
        };
      case 'teacher':
        return {
          title: 'Teacher Dashboard',
          description: 'Manage your classes and student assessments',
          cards: [
            {
              title: 'Post Results',
              description: 'Add and update student results for your subjects',
              link: '/results',
              count: stats.totalResults
            },
            {
              title: 'Student Evaluations',
              description: 'Evaluate student performance and behavior',
              link: '/evaluations',
              count: stats.totalEvaluations
            }
          ]
        };
      case 'student':
        return {
          title: 'Student Dashboard',
          description: 'View your academic progress and evaluations',
          cards: [
            {
              title: 'My Results',
              description: 'Check your latest academic results',
              link: '/results',
              count: stats.totalResults
            },
            {
              title: 'My Evaluations',
              description: 'View teacher feedback and evaluations',
              link: '/evaluations',
              count: stats.totalEvaluations
            }
          ]
        };
      case 'parent':
        return {
          title: 'Parent Dashboard',
          description: 'Stay updated with your child\'s progress',
          cards: [
            {
              title: 'Child\'s Results',
              description: 'View your child\'s academic performance',
              link: '/results',
              count: stats.totalResults
            },
            {
              title: 'Submit Review',
              description: 'Share feedback about the school',
              link: '/reviews',
              count: stats.totalReviews
            },
            {
              title: 'View Evaluations',
              description: 'Check teacher evaluations for your child',
              link: '/evaluations',
              count: stats.totalEvaluations
            }
          ]
        };
      default:
        return {
          title: 'Dashboard',
          description: 'Welcome to SchoolHub',
          cards: []
        };
    }
  };

  const content = getRoleSpecificContent();

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="space-y-2">
        <h1 className="text-3xl font-bold text-gray-900">
          {content.title}
        </h1>
        <p className="text-gray-600">
          Welcome back, {user.name}! {content.description}
        </p>
      </div>

      {/* Quick Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Results</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalResults}</div>
            <p className="text-xs text-gray-600">Academic results posted</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Total Evaluations</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalEvaluations}</div>
            <p className="text-xs text-gray-600">Evaluations completed</p>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
            <CardTitle className="text-sm font-medium">Parent Reviews</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{stats.totalReviews}</div>
            <p className="text-xs text-gray-600">Reviews submitted</p>
          </CardContent>
        </Card>
      </div>

      {/* Action Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {content.cards.map((card, index) => (
          <Card key={index} className="hover:shadow-md transition-shadow">
            <CardHeader>
              <CardTitle className="flex items-center justify-between">
                {card.title}
                <span className="text-sm font-normal bg-gray-100 px-2 py-1 rounded">
                  {card.count}
                </span>
              </CardTitle>
              <CardDescription>{card.description}</CardDescription>
            </CardHeader>
            <CardContent>
              <Link href={card.link}>
                <Button className="w-full">
                  Go to {card.title}
                </Button>
              </Link>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Recent Activity */}
      <Card>
        <CardHeader>
          <CardTitle>Recent Activity</CardTitle>
          <CardDescription>
            Latest updates and activities in the system
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            <div className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
              <div>
                <p className="font-medium">System initialized with sample data</p>
                <p className="text-sm text-gray-600">Welcome to SchoolHub!</p>
              </div>
              <span className="text-xs text-gray-500">Just now</span>
            </div>
            
            {stats.totalResults > 0 && (
              <div className="flex items-center justify-between p-3 bg-blue-50 rounded-lg">
                <div>
                  <p className="font-medium">Student results available</p>
                  <p className="text-sm text-gray-600">{stats.totalResults} results posted</p>
                </div>
                <span className="text-xs text-gray-500">Recent</span>
              </div>
            )}

            {stats.totalEvaluations > 0 && (
              <div className="flex items-center justify-between p-3 bg-green-50 rounded-lg">
                <div>
                  <p className="font-medium">Evaluations completed</p>
                  <p className="text-sm text-gray-600">{stats.totalEvaluations} evaluations available</p>
                </div>
                <span className="text-xs text-gray-500">Recent</span>
              </div>
            )}

            {stats.totalReviews > 0 && (
              <div className="flex items-center justify-between p-3 bg-purple-50 rounded-lg">
                <div>
                  <p className="font-medium">Parent reviews submitted</p>
                  <p className="text-sm text-gray-600">{stats.totalReviews} reviews available</p>
                </div>
                <span className="text-xs text-gray-500">Recent</span>
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
